import React from 'react';
import { renderToString } from 'react-dom/server'

const Props = ({ props }) => {
  props.forEach(prop => {
    console.log(prop.name, '|', prop.type, '|', prop.defaultValue, '|', typeof prop.description === 'object' ? renderToString(prop.description) : prop.description, '|');
  });

  return (
    <>
      <div className="container">
        <table className="table">
          <thead>
            <tr>
              <th>Prop name</th>
              <th>Type</th>
              <th>Default value</th>
              <th>Description</th>
            </tr>
          </thead>
          <tbody>
            {(props || []).map(prop => (
              <tr key={prop.name}>
                <td>
                  <strong>{prop.name}</strong>
                </td>
                <td>{prop.type}</td>
                <td>{prop.defaultValue}</td>
                <td>{prop.description}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
      <style jsx>{`
        .container {
          width: 90%;
          overflow: scroll;
        }

        .table {
          border-spacing: 1px;
          border-collapse: collapse;
          border: 1px solid #ddd;
        }

        .table,
        .table th,
        .table td {
          border-collapse: collapse;
          text-align: left;
        }

        .table tr {
          color: black;
          background-color: white;
          border-top: 1px solid #c6cbd1;
        }

        .table tr:nth-child(2n) {
          background-color: #f6f8fa;
        }

        .table td,
        .table th {
          padding: 6px 13px;
          border: 1px solid #dfe2e5;
        }
      `}</style>
    </>
  );
};

export default Props;
